<template>
  <div class="register">
    <a-form
      id="components-form-demo-normal-login"
      :form="form"
      class="login-form"
      @submit="handleSubmit"
    >
      <a-form-item>
        <a-input
          v-decorator="[
          'username',
          { rules: [{ required: true, message: '请输入你的用户名!' }] }
        ]"
          placeholder="用户名"
        >
          <a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
        </a-input>
      </a-form-item>
      <a-form-item>
        <a-input
          v-decorator="[
          'password',
          { rules: [{ required: true, message: '请输入你的密码!' }] }
        ]"
          type="password"
          placeholder="密码"
        >
          <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
        </a-input>
      </a-form-item>

      <a-form-item>
        <a-input
          v-decorator="[
          'nickname',
          { rules: [{ required: true, message: '请输入你的昵称!' }] }
        ]"
          placeholder="昵称"
        >
          <a-icon slot="prefix" type="smile" style="color: rgba(0,0,0,.25)" />
        </a-input>
      </a-form-item>

      <a-form-item class="checkForm">
        <a-checkbox
          v-decorator="['agreement',{rules:[{required: true},{validator:userNeedAgreement}]}]">
          我已阅读并同意
          <a href>使用条框</a>
        </a-checkbox>
      </a-form-item>

      <a-form-item>
        <a-button type="primary" html-type="submit" class="login-form-button">注册</a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import {message} from 'ant-design-vue'

export default {
  name: "RegisterView",
  data() {
    return {};
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  methods: {
      ...mapActions(["registerWebIM"]),
      userNeedAgreement(rule, value, callback) {
      if (!value) {
        callback("你还未同意使用协议!");
      } else {
        callback();
      }
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
            this.registerWebIM(values).then(() => {
                console.log('注册成功！');
                message.success('注册成功！');
                this.$router.replace('./')
            }, (error) => {
                console.log(error.data);
                message.error('用户名已存在，注册失败！');
            })
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.register{
    padding: 100px 0;
    margin: 0 auto;
    width: 85%;
#components-form-demo-normal-login {
  .login-form-register {
    float: left;
  }

  .login-form-forgot {
    float: right;
  }
  .checkForm{
      float: left;
  }
  .login-form-button{
      width: 100%;
  }
}
}

</style>